import AspectRatio from "@mui/joy/AspectRatio";
import Box from "@mui/joy/Box";
import Typography from "@mui/joy/Typography";
import Card from "@mui/joy/Card";

import React from "react";
import CustomCard from "../../components/UI/customCard/CustomCard";
import CustomCardWithMedia from "../../components/UI/customCard/CustomCardWithMedia";

import avatar from "../../images/avatar2.jpg";
import consultant from "../../images/mentalConsultant.jpeg";
import services from "../../images/services.jpeg";

import { content, content1, content2 } from "./content";
import { Button, Grid, useMediaQuery } from "@mui/material";
import {  useNavigate } from "react-router-dom";

const Info = () => {
  const matches = useMediaQuery("(min-width:768px)");

  const navigate = useNavigate();
  const navigateToAboutUs = () => {
    navigate("/hakkimda");
  };

  const navigateToServices = () => {
    navigate("/hizmetler");
  };

  const data = [
    {
      src: avatar,
      title: "Psikolojik Danışman Büşra Çırak",
      description: "Detaylı Bilgi İçin Tıklayınız.",
      action: navigateToAboutUs,
    },
    {
      src: consultant,
      title: "Psikolojik Danışmanlık Nedir ?",
      description: "Detaylı Bilgi İçin Tıklayınız.",
      action:navigateToAboutUs,
    },
    {
      src: services,
      title: "Hizmetler",
      description: "Detaylı Bilgi İçin Tıklayınız.",
      action:navigateToServices
    },
  ];

  return (
    <Box
      sx={{
        backgroundColor: "white",
        justifyContent: "center",
        display: "flex",
        userSelect: "none",
      }}
      mb={3}
    >
      {matches ? (
        <Grid container spacing={6} justifyContent="center" m={5} p="2rem 0rem">
          <Grid item sm={4}>
            <CustomCardWithMedia
              title="Psikolojik Danışman Büşra Çırak"
              content={content}
              action="Daha Fazla"
              navigate={navigateToAboutUs}
            />
          </Grid>
          <Grid item sm={4}>
            <CustomCard
              title="Psikolojik Danışmanlık Nedir ?"
              content={content1}
              action="Daha Fazla"
              page="hakkimda"
              navigate={navigateToAboutUs}
              height={570}
            />
          </Grid>
          <Grid item sm={4}>
            <CustomCard
              title="Hizmetlerimiz Neler ?"
              content={content2}
              action="Daha Fazla"
              page="hizmetler"
              navigate={navigateToServices}
              height={570}
            />
          </Grid>
        </Grid>
      ) : (
        <Box
          sx={{
            display: "flex",
            gap: 1,
            pt: 3,
            overflow: "auto",
            width: "95%",
            scrollSnapType: "x mandatory",
            "& > *": {
              scrollSnapAlign: "center",
            },
            "::-webkit-scrollbar": { display: "none" },
          }}
        >
          {data.map((item) => (
            <Card
              orientation="horizontal"
              key={item.title}
              variant="soft"
              sx={{
                gap: 2,
                "--Card-padding": (theme) => theme.spacing(2),
              }}
            >
              <AspectRatio ratio="1" sx={{ minWidth: 100 }}>
                <img src={item.src} srcSet={item.src} alt={item.title} />
              </AspectRatio>
              <Box sx={{ whiteSpace: "nowrap" }}>
                <Typography fontWeight="lg">{item.title}</Typography>
                <Typography mt={1.5} level="body2">
                  {item.description}
                </Typography>
                <Button
                  onClick={item.action}
                  color="info"
                  variant="contained"
                  size="small"
                  sx={{
                    marginTop: ".7rem",
                  }}
                >
                  Daha Fazla
                </Button>
              </Box>
            </Card>
          ))}
        </Box>
      )}
    </Box>
  );
};

export default Info;
