import {
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Typography,
} from "@mui/material";
import { useNavigate } from "react-router-dom";

import avatar from "../../../images/avatar2.jpg";
import classes from "./custom-card.module.css";

export default function CustomCardWithMedia(props) {
  const navigate = useNavigate();

  const navigateToAboutUs = () => {
    navigate("hakkimda");
  };

  return (
    <Card
      className={classes.card}
      sx={{
        minWidth: 325,
        minHeight: 570,
        maxHeight: 500,
        borderRadius: "1rem",
        opacity: 0.85,
      }}
    >
      <CardMedia
        component="img"
        sx={{
          minHeight: 300,
          maxHeight: 510,
          minWidth: 325,
        }}
        image={avatar}
        title="doctor"
      />
      <CardContent>
        <Typography
          variant="h5"
          component="div"
          color="text.secondary"
          gutterBottom
        >
          {props.title}
        </Typography>
        <br />
        <Typography variant="body2">
          {props.content}
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button
          onClick={navigateToAboutUs}
          variant="contained"
          color="info"
          size="small"
          sx={{ margin: ".5rem" }}
        >
          {props.action}
        </Button>
      </CardActions>
    </Card>
  );
}
