import React from "react";

import appointmentPicture from "../../images/appointment.jpeg";
import classes from "./main-page.module.css";

//icons
import InstagramIcon from "@mui/icons-material/Instagram";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import EmailIcon from "@mui/icons-material/Email";

import Fab from "@mui/material/Fab";
import { Box, Button, Grid, Typography, useMediaQuery } from "@mui/material";

const message = "Merhaba, randevu almak istiyorum.";
const phone = "9005423822662";
const encodedMessage = encodeURIComponent(message);
const whatsappLink = `https://api.whatsapp.com/send?phone=${phone}&text=${encodedMessage}`;
export default function Appointment() {
  const matches = useMediaQuery("(min-width:768px)");

  return (
    <Box className={classes.appointment}>
      <img src={appointmentPicture} alt="appointment" />

      {matches ? (
        <Grid
          container
          direction="column"
          justifyContent="center"
          alignItems="center"
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            zIndex: 1,
          }}
        >
          <Grid item xs={12}>
            <Typography
              variant="h4"
              sx={{
                color: "white",
                textAlign: "center",
                userSelect: "none",
              }}
            >
              Kendinizi daha iyi hissetmek için bir adım yeterli
            </Typography>
          </Grid>

          <Grid item xs={12}>
            <Button
              href="tel:05423822662"
              variant="contained"
              sx={{
                textAlign: "center",
                marginTop: 2,
              }}
            >
              Randevu Alın
            </Button>
          </Grid>
        </Grid>
      ) : (
        <Box>
          <Typography
            variant="body1"
            sx={{
              position: "absolute",
              top: "30%",
              left: "35%",
              transform: "translate(-50%, -50%)",
              color: "white",
              textAlign: "start",
              userSelect: "none",
            }}
          >
            Kendinizi daha iyi hissetmek için bir adım yeterli
          </Typography>
          <Button
            size="small"
            href="tel:05423822662"
            variant="contained"
            sx={{
              position: "absolute",
              top: "70%",
              left: "16%",
              transform: "translate(-50%, -50%)",
              textAlign: "center",
            }}
          >
            Randevu Alın
          </Button>
        </Box>
      )}

      {matches ? (
        <Box
          sx={{
            "& > :not(style)": { m: 1 },
            position: "absolute",
            display: "flex",
            justifyContent: "flex-end",
            top: "70%",
            right: "5%",
            color: "white",
          }}
        >
          <Fab
            target="_blank"
            color="secondary"
            aria-label="instagram"
            href="https://instagram.com/psk.danbusra?igshid=YmMyMTA2M2Y="
          >
            <InstagramIcon />
          </Fab>
          <Fab
            href={whatsappLink}
            color="success"
            aria-label="whatsapp"
            target="_blank"
          >
            <WhatsAppIcon />
          </Fab>
          <Fab
            target="_blank"
            href="mailto:busracirak04@gmail.com"
            color="info"
            aria-label="mail"
          >
            <EmailIcon />
          </Fab>
        </Box>
      ) : (
        <Box
          sx={{
            "& > :not(style)": { m: 1 },
            position: "absolute",
            display: "flex",
            justifyContent: "flex-end",
            top: "55%",
            right: "3%",
            color: "white",
          }}
        >
          <Fab
            target="_blank"
            color="secondary"
            aria-label="instagram"
            href="https://instagram.com/psk.danbusra?igshid=YmMyMTA2M2Y="
            size="small"
          >
            <InstagramIcon />
          </Fab>
          <Fab
            href={whatsappLink}
            color="success"
            aria-label="whatsapp"
            target="_blank"
            size="small"
          >
            <WhatsAppIcon />
          </Fab>
          <Fab
            target="_blank"
            href="mailto:busracirak04@gmail.com"
            color="info"
            aria-label="mail"
            size="small"
          >
            <EmailIcon />
          </Fab>
        </Box>
      )}
    </Box>
  );
}
