import {
  Button,
  Card,
  CardActions,
  CardContent,
  Typography,
} from "@mui/material";

import classes from './custom-card.module.css';

export default function CustomCard(props) {

  return (
    <Card
      className={classes.card}
      sx={{
        minWidth: 325,
        minHeight: props.height,
        opacity: .85,
        maxHeight: 650,
        borderRadius:'1rem'
      }}
    >
      <CardContent>
        <Typography
          variant="h5"
          component="div"
          color="text.secondary"
          gutterBottom
        >
          {props.title}
        </Typography>
        <br />
        <Typography variant="body2">
          {props.content}
        </Typography>
      </CardContent>
      <CardActions>
        <Button sx={{margin:'.5rem'}} onClick={props.navigate} variant='contained' color='info' size="small">
          {props.action}
        </Button>
      </CardActions>
    </Card>
  );
}
